<template>
  <div>
    <el-form :inline="true">
      <el-row style="height:24px">
        <el-col :span="24" style="height:24px;text-align: left">
          <el-form-item label="">
            <law-code @code-change="codeChange"></law-code>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <el-divider></el-divider>
    <el-row style="height:24px">
      <el-col :span="24" style="height:24px;text-align: left">
        <i class="el-icon-office-building"></i>  <span v-if="tableData.length > 0"> : {{ tableData.length }} </span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table
            :data="tableData"
            v-loading="loading"
            element-loading-text="Loading..."
            @row-click="rowClick"
            :header-cell-style="{ background: '#000', textAlign: 'center' }"
            height="300"
            border
            style="width: 100%">
          <el-table-column
              prop="apartment_name"
              label=""
              width="280">
          </el-table-column>
          <el-table-column
              prop="dong"
              label="">
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row style="height:24px">
      <el-col :span="24" style="height:24px;text-align: left">
        <i class="el-icon-edit-outline"></i>  <span v-if="silTrade.length > 0"> : {{ silTrade.length }} </span>

        <span v-if="silTrade.length > 0">&nbsp;&nbsp;<el-button :disabled="silTrade.length > 0 ? false : true"
                                                                @click="download" type="primary">Download<i
            class="el-icon-download el-icon-right"></i></el-button></span>
      </el-col>
    </el-row>
    <el-row v-if="filters1.length > 0">
      <el-col :span="24" style="height:24px;text-align: left">
        <div>
          <el-select v-model="radio1" @change="chg()" placeholder="">
            <el-option
                v-for="item in filters1"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>

          <el-select v-model="radio2" @change="chg()" placeholder="">
            <el-option
                v-for="item in filters2"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>

          <el-select v-model="radio3" @change="chg()" placeholder="">
            <el-option
                v-for="item in filters3"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>

        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table
            :data="silTrade"
            v-loading="loading"
            element-loading-text="Loading..."
            :header-cell-style="{ background: '#000', textAlign: 'center' }"
            height="300"
            border
            style="width: 100%">
          <el-table-column
              prop=""
              label=""
              width="80">
          </el-table-column>
          <el-table-column
              prop=""
              label=""
              width="100">
          </el-table-column>
          <el-table-column
              prop=""
              label=""
              width="280">
          </el-table-column>
          <el-table-column
              prop=""
              label=""
              width="80">
          </el-table-column>
          <el-table-column
              prop=""
              label=""
              width="80">
          </el-table-column>
          <el-table-column
              prop=""
              label=""
              width="80">
          </el-table-column>
          <el-table-column
              prop=""
              label=""
              width="80">
          </el-table-column>
          <el-table-column
              prop=""
              label=""
              width="100">
          </el-table-column>
          <el-table-column
              prop=""
              label=""
              width="80">
          </el-table-column>
          <el-table-column
              prop=""
              label=""
              width="80">
          </el-table-column>
          <el-table-column
              prop=""
              label=""
              width="80">
          </el-table-column>
          <el-table-column
              prop=""
              label=""
              width="80">
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" style="height:24px;text-align: left">
        <i class="el-icon-data-line"></i> Chart
      </el-col>
    </el-row>
    <el-row v-if="visible == true && chartData.length > 0">
      <el-col :span="24">
        <trade-chart :chartData="chartData" :chartCategory="chartCate"></trade-chart>
      </el-col>
    </el-row>
  </div>


</template>
<script>
import LawCode from "@/components/LawCode";
import TradeChart from "@/components/TradeChart";

export default {
  components: {
    LawCode, TradeChart
  },

  created() {
    this.getData();

    this.$axios.interceptors.request.use((config) => {
      this.loading = true;
      return config;
    }, (error) => {
      this.loading = false;
      return Promise.reject(error);
    });

    this.$axios.interceptors.response.use((response) => {
      this.loading = false;
      return response;
    }, (error) => {
      this.loading = false;
      return Promise.reject(error);
    });

  },
  data() {
    return {
      rows: [],
      tableData: [],
      silTrade: [],
      trades: [],
      loading: false,
      visible : false,
      dealTypes: [],
      areas: [],
      floors: [],
      radio1: "",
      radio2: "",
      radio3: "",
      filters1: [],
      filters2: [],
      filters3: [],
      chartData: [],
      chartCate: [],
    }
  },
  computed: {
    disabled() {
      return this.silTrade.length > 0 ? true : false;
    }
  },
  methods: {
    codeChange(lastCate) {
      let apts = this.rows.filter(row => row[0].substring(0, 10) === lastCate);
      let tableData = [];
      apts.map(e => tableData.push({
        "acode": e[0],
        "apartment_name": e[1],
        "dong": e[2],
      }))

      this.tableData = tableData;
    },
    getData() {
      this.$axios.get('/apt/' + 'apt_code.csv', {params: {}})
          .then((response) => {
            let rows = [];
            let r = response.data.replace(/["]+/g, "").split("\n");
            r.map(e => rows.push(e.split(",")))
            this.rows = rows;
            this.loading = false;
          });
    },
    rowClick(row) {
      this.$axios.get('/apt/' + row.acode + '.csv', {params: {}})
          .then((response) => {
            let data = response.data.replace(/["]+/g, "").split("\n");
            let rows = [];
            data.map(e => e.length > 0 ? rows.push(e.split(",")) : 1 == 1)
            let silTrade = [];
            let k = 0;
            rows.map(e => silTrade.push({
              : ++k,
              : e[0],
              : e[1],
              : e[2],
              : e[3],
              : e[4],
              : e[5],
              : e[6],
              : e[9] + "-" + this.dateType(e[8]) + "-" + this.dateType(e[7]),
              : e[10],
              : e[11],
              : e[12],
              : e[13],
              : e[14],
              : e[15],
              : this.dealType(e[16], e[15]),
            }))
            this.dealTypes = this.groupKey(silTrade, "");
            this.areas = this.groupKey(silTrade, "");
            this.floors = this.groupKey(silTrade, "");
            this.silTrade = silTrade;
            this.trades = silTrade;

            this.filters1 = Object.keys(this.dealTypes)
            this.filters2 = Object.keys(this.areas)
            this.filters3 = Object.keys(this.floors)

            this.filters1.unshift("");
            this.filters2.unshift("");
            this.filters3.unshift("");

            this.createChart();

          });
    },
    dealType(type, rent) {
      if (type === 'N') {
        return "";
      } else {
        if (parseInt(rent) > 0) {
          return "";
        } else {
          return ""
        }
      }
    },
    chg() {
      this.silTrade = this.trades;

      if (this.radio1 != "" && this.radio1 != "") {
        this.silTrade = this.silTrade.filter(e => e[""] == this.radio1)
      } else {
        this.silTrade = this.silTrade.filter(e => e[""].length > 0)
      }

      if (this.radio2 != "" && this.radio2 != "") {
        this.silTrade = this.silTrade.filter(e => e[""] == this.radio2)
      } else {
        this.silTrade = this.silTrade.filter(e => e[""].length > 0)
      }

      if (this.radio3 != "" && this.radio3 != "") {
        this.silTrade = this.silTrade.filter(e => e[""] == this.radio3)
      } else {
        this.silTrade = this.silTrade.filter(e => e[""].length > 0)
      }
      this.createChart();
    },
    download() {
      this.excelDownload(this.silTrade, "", this.silTrade[0][""]);
    },
    createChart() {
      this.visible = false;
      this.chartCate = [];
      this.chartData = [];

      this.chartCate.push("");
      this.chartCate.push("");
      this.chartCate.push("");

      let dt = [];
      let dt2 = [];
      let dt3 = [];

      this.silTrade.map(ele => {
        if(ele[""] == "") {
          dt.push([ele[""], ele[""], ele[""], ele[""], ele[""], ele[""], ele[""]])
        } else if(ele[""] == "") {
          dt2.push([ele[""], ele[""], ele[""], ele[""], ele[""], ele[""], ele[""]])
        } else {
          dt3.push([ele[""], ele[""], ele[""], ele[""], ele[""], ele[""], ele[""]])
        }
      });


      let chartData = {
        name: "",
        symbolSize: 6,
        data: dt,
        markPoint: {
          symbol: 'pin',
          data: [{type: 'max', name: 'max'},],
          itemStyle: {
            color: '#3A1079'
          },
        },
        type: 'scatter'
      };

      this.chartData.push(chartData);

      chartData = {
        name: "",
        symbolSize: 6,
        data: dt2,
        markPoint: {
          symbol: 'pin',
          data: [{type: 'max', name: 'max'},],
          itemStyle: {
            color: '#3A1079'
          },
        },
        type: 'scatter'
      };

      this.chartData.push(chartData);

      chartData = {
        name: "",
        symbolSize: 6,
        data: dt3,
        markPoint: {
          symbol: 'pin',
          data: [{type: 'max', name: 'max'},],
          itemStyle: {
            color: '#3A1079'
          },
        },
        type: 'scatter'
      };

      this.chartData.push(chartData);

      this.visible = true;
    }
  }
}
</script>