<template>
  <v-chart ref="chart" class="chart" :option="option" />
</template>

<script>
import VChart, { THEME_KEY } from "vue-echarts";
import 'echarts'

function getChartImg(name){

  //  .
  let canvas = document.getElementsByTagName("canvas");

  if(canvas&&canvas.length>0){
    // 
    let tempA = document.createElement("a");
    //  
    tempA.download = name +".png";
    //address    
    tempA.href = canvas[0].toDataURL("image/png");
    document.body.appendChild(tempA);
    //  
    tempA.click();
    // 
    tempA.remove();
  }
}
export default {
  name: "TradeChart",
  props: {
    chartData : {
      type : Array,
    },
    chartCategory : {
      type : Array,
    },
    chartTitle : {
      type : String,
    }
  },
  components: {
    VChart
  },
  provide: {
    [THEME_KEY]: "vintage"
  },
  data() {
    return {
      option: {
        backgroundColor: '#fef8ef',
        feature: {
          saveAsImage: {}
        },
        grid: {
          left: '10%',
          right: '10%',
          top: '5%',
          bottom: '5%',
        },
        title: {
          text: this.chartTitle,
          left: "center"
        },
        legend: {
          data : this.chartCategory
        },
        tooltip: {
          formatter:function(obj) {
            let value = obj.value;
            return '<div style="border-bottom: 1px solid rgba(255,255,255,.3); font-size: 18px;padding-bottom: 7px;margin-bottom: 7px">'
                + '-  : ' + value[0] +'<br />'
                + '-  ' + value[1] + '<br/>'
                + '-   ' + value[2] + '<br/>'
                + '-  ' + value[3] + '<br/>'
                + '-  ' + value[4] + '<br/>'
                + '-  ' + value[5] + '<br/>'
                + '-  ' + value[6] + '<br/>'
                + '</div>';
          } ,
          axisPointer: {
            type: 'cross'
          }
        },
        xAxis: {
          // type: 'category',
          name: '',
          scale: true,
          type: 'time',
          distribution: 'linear',
          time: {
            unit: 'day'
          },
          splitLine: {
            lineStyle: {
              type: 'dashed'
            }
          },
          splitNumber: 10,
          nameTextStyle:{
            fontSize : 10
          }
        },
        yAxis: {
          type:'value',
          name:'',
          splitLine: {
            lineStyle: {
              type: 'dashed'
            }
          },
          nameTextStyle:{
            fontSize : 10
          }
        },
        series: this.chartData
      }
    };
  },
  watch: {
    chartData: {
      handler() {
        this.option.series = this.chartData;
      },
      deep: true
    }
  },
  methods: {
    downloadChart(name) {
      getChartImg(name);
    }
  }
};
</script>

<style scoped>
.echarts {
  width: 100%;
  height: 700px;
}
</style>